






























































import { Component, Vue } from 'vue-property-decorator';

@Component({
  metaInfo: {
    title: 'Home',
  },
})

export default class Home extends Vue {
  private hospitals = [];

  private mounted() {
    this.getHospitals();
  }

  private getHospitals() {
    this.axios.get('health-center')
      .then((res) => {
        this.hospitals = res.data;
      })
      .catch((err) => {
        //
      });
  }
}
